.list {
    display: grid;
    max-width: calc(100vw - 48px);
    grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
    grid-gap: 16px;
    margin-top: 0;
    margin-bottom: 0;
    padding: 0;
    list-style: none;
    margin-left: auto;
    margin-right: auto;
}

.listItem {
    width: 100%;
    height: auto;
    object-fit: cover;
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.listItem:hover {
    transform: scale(1.03);
    cursor: zoom-in;
}

.image {
    width: 250px;
    height: 350px;
    border-radius: 4px;
    object-fit: cover;
}
